import OwlSay from '../Components/OwlSay'
import React from 'react'
import BackButton from '../Components/BackButton'
import { homepage } from '../../package.json'
import { useTrackingCode } from 'react-hubspot-tracking-code-hook'

const OnlineAccounts = () => {
  const { setPathPageView } = useTrackingCode()
  setPathPageView(OnlineAccounts.path)

  return (
    <>
      <OwlSay>Online photos? Venmo?</OwlSay>
      <BackButton>Go back</BackButton>
    </>
  )
}

OnlineAccounts.path = `${homepage}/online-accounts`

export default OnlineAccounts
