import React from 'react'
import OwlSay from '../Components/OwlSay'
import BackButton from '../Components/BackButton'
import { useTrackingCode } from 'react-hubspot-tracking-code-hook'

const Pets = () => {
  const { setPathPageView } = useTrackingCode()
  setPathPageView(Pets.path)

  return (
    <>
      <OwlSay>
        It is important to think about your pets. Who will care for them when
        you can't?
      </OwlSay>
      <BackButton></BackButton>
    </>
  )
}

Pets.path = '/pets'

export default Pets
