import * as PropTypes from 'prop-types'
import React from 'react'
import { homepage } from '../../package.json'

function OwlSay({ wide, children }) {
  const showWide = children.length > 170 || wide
  return (
    <div className="OwlfredAgent">
      <img
        className="owlfred"
        src={`${homepage}/Owlfred.svg`}
        alt="Helpful owl"
      />
      <span className={`bubble ${showWide && 'bubble-wide'}`}>{children}</span>
    </div>
  )
}
OwlSay.propTyoes = {
  wide: PropTypes.bool,
}

export default OwlSay
