import OwlSay from '../Components/OwlSay'
import React from 'react'
import BackButton from '../Components/BackButton'
import ProbateOverviewArticle from '../Articles/ProbateProcessOverview.md'
import ReactMarkdown from 'react-markdown'
import { useTrackingCode } from 'react-hubspot-tracking-code-hook'

const Probate = () => {
  const { setPathPageView } = useTrackingCode()
  setPathPageView(Probate.path)

  return (
    <>
      <OwlSay>
        In the US, the probate process varies by state. Here is a brief article
        that describes the usual series of events. If you find this information
        overwhelming or confusing or you just don't want to deal with it now,
        that's ok. Set it aside. I will guide you through it in small increments
        as we go through the process of planning your legacy.
      </OwlSay>
      <ReactMarkdown source={ProbateOverviewArticle} />
      <BackButton />
    </>
  )
}

Probate.path = '/probate'

export default Probate
