import React from 'react'
import OwlSay from '../Components/OwlSay'
import BackButton from '../Components/BackButton'
import { homepage } from '../../package.json'
import { useTrackingCode } from 'react-hubspot-tracking-code-hook'

const Firearms = () => {
  const { setPathPageView } = useTrackingCode()
  setPathPageView(Firearms.path)

  return (
    <>
      <OwlSay wide={true}>
        <p>
          Since the ownership of firearms has some restrictions, I recommend
          that you set up a living trust. This ensures that your firearms will
          transfer to your heirs, regardless of how restrictions or their right
          to own them change in the future. If they have their right to possess
          firearms amended, they will still be able to own them long enough to
          legally sell them.
        </p>
        <p>
          I'm not currently able to help you set up a living trust for firearms,
          but I can recommend and attorney that can. Do you want to see a list
          of services that specialize in setting up trusts for firearms?
        </p>
      </OwlSay>
      <div className="menu">
        <a
          href="https://www.google.com/search?q=firearm+living+trust"
          target="_blank"
          className="btn btn-primary"
          rel="noopener noreferrer">
          Show me who can help me set up a firearm living trust
        </a>
      </div>
      <BackButton />
    </>
  )
}

Firearms.path = `${homepage}/firearms`

export default Firearms
