import OwlSay from '../Components/OwlSay'
import React from 'react'
import BackButton from '../Components/BackButton'
import MenuButton from '../Components/MenuButton'
import Introduction from './Introduction'
import { homepage } from '../../package.json'
import { useTrackingCode } from 'react-hubspot-tracking-code-hook'

const Euphemisms = () => {
  const { setPathPageView } = useTrackingCode()
  setPathPageView(Euphemisms.path)

  return (
    <>
      <OwlSay>How do you want to refer to your own death?</OwlSay>
      <div className="menu">
        <MenuButton to={Introduction.path}>Carried away by angels</MenuButton>
        <MenuButton to={Introduction.path}>Bite the dust</MenuButton>
        <MenuButton to={Introduction.path}>Croaked</MenuButton>
        <MenuButton to={Introduction.path}>Fragged</MenuButton>
        <MenuButton to={Introduction.path}>Kicked the bucket</MenuButton>
        <MenuButton to={Introduction.path}>Left the building</MenuButton>
      </div>
      <BackButton>Go back</BackButton>
    </>
  )
}

Euphemisms.path = `${homepage}/euphemisms`

export default Euphemisms
