import React from 'react'
import OwlSay from '../Components/OwlSay'
import BackButton from '../Components/BackButton'
import ItemForm from '../Components/ItemForm'
import violinImage from '../stock-images/girl-playing-the-violin.jpg'
import classicCarImage from '../stock-images/man-polishing-classic-car.jpg'
import trophyImage from '../stock-images/football-players-celebrating.jpg'
import weddingDressImage from '../stock-images/wedding-dress.jpg'
import { homepage } from '../../package.json'
import { useTrackingCode } from 'react-hubspot-tracking-code-hook'

const sentimentalItemPhotoList = [
  violinImage,
  classicCarImage,
  trophyImage,
  weddingDressImage,
]

const SentimentalItems = () => {
  const { setPathPageView } = useTrackingCode()
  setPathPageView(SentimentalItems.path)

  return (
    <>
      <OwlSay>
        Tell me about your item. Why is it important? Is there a story behind
        it?
      </OwlSay>
      <ItemForm photoAlbumSet={sentimentalItemPhotoList} />
      <BackButton></BackButton>
    </>
  )
}

SentimentalItems.path = `${homepage}/sentimental-items`

export default SentimentalItems
