import React from 'react'
import { useHistory } from 'react-router-dom'
import { Button } from 'react-bootstrap'
import '../bootstrap-directional-buttons.min.css'

function BackButton() {
  const history = useHistory()
  return (
    <Button className="btn-menu-back" onClick={history.goBack}>
      <div className="menu-btn-text">Go back</div>
    </Button>
  )
}

export default BackButton
